import { createRouter, createWebHashHistory } from 'vue-router'
import BiuderHome from "@/views/BiuderHome"


let otherRouter = [
  { path: "/", redirect: '/BiuderHome.html' },
  { path: "/BiuderHome.html", name: "ÂíïÂôúÂíïÂôú", component: BiuderHome },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes: []
    .concat(otherRouter)
});
router.beforeEach((to, from, next) => {
  next()
});
router.afterEach((transition) => {
  let name = transition.name
  let item = router.options.routes.filter((ele) => { return ele.name == name })
  setTitle(item[0].name)
});
function setTitle(title) {
  document.title = title
  var mobile = navigator.userAgent.toLowerCase()
  if (/iphone|ipad|ipod/.test(mobile)) {
    var iframe = document.createElement("iframe")
    iframe.style.display = "none"
    var iframeCallback = function () {
      setTimeout(function () {
        iframe.removeEventListener("load", iframeCallback)
        document.body.removeChild(iframe)
      }, 0)
    }
    iframe.addEventListener("load", iframeCallback)
    document.body.appendChild(iframe)
  }
}
export default router
